 * {
    box-sizing: border-box;
    margin: 0;
    padding:0;
 }

 html {
    font-size: 62.5%;
 }

 $colorwhite: #fff;
 $bright-orange: #f26002;
 $black: #000;
 $greyish-brown: #4a4a4a;
 $white-two: #f2f2f2;

 .burguer {
   width:50%;
 }

.burguer-box {
   box-sizing: border-box;
   height: 20px;
   width: 20px;
   background-color: $bright-orange;
   position:fixed;
   display: flex;
   flex-direction: row;
   justify-content: center;
   z-index: 3;
}
.hero {
   height: 580px;
   padding-bottom: 10px;
   background-image: url("../images/cover.jpeg");
   background-repeat: no-repeat;
   background-position: left;
   background-size: cover;
   display: flex;
   flex-direction: column;
   justify-content: flex-end;
   align-items:center;
   text-align: center;
   color:#fff;
   

}
.title {
   font-size: 2rem;
}
.promo-1 { 
font-family:'Roboto', sans-serif;
padding: 10px 0;
font-size: 1.5rem;
font-weight: 300;
line-height: 1;
}

.scroll-down {
   width:15px;
}
.white-circle {
   background-color: #fff;
   width: 23px;
   height: 23px;
   display: flex;
   justify-content: center;
   align-items: center;
   border-radius: 52%;
}

 
 




